import React, { Component } from 'react'

class SimpleAuditCheck extends Component {
    constructor(props) {
        super(props)

        this.state = {
            cashIn: 0,
            bonus: 0,
            winLose: 0,
            outstanding: 0,
            cashOut: 0,
            auditCheck: 0,
            result: "",
            textColor: 'gray'
        }
    }

    handleCashInChange = (event) => {
        this.setState({
            cashIn: event.target.value
        })
    }

    handleBonusChange = (event) => {
        this.setState({
            bonus: event.target.value
        })
    }

    handleWinLoseChange = (event) => {
        this.setState({
            winLose: event.target.value
        })
    }

    handleOutstandingChange = (event) => {
        this.setState({
            outstanding: event.target.value
        })
    }

    handleCashOutChange = (event) => {
        this.setState({
            cashOut: event.target.value
        })
    }

    handleSubmit = (event) => {
        event.preventDefault()
        var { cashIn, bonus, winLose, outstanding, cashOut } = this.state
        var auditNumber = Number(cashIn) + Number(bonus) + Number(winLose) + Number(outstanding) - Number(cashOut)
        this.setState({
            auditCheck: auditNumber.toFixed(2)
        })

        if (auditNumber == 0) {
            this.setState({
                result: "Valid",
                textColor:"green"
            })
        } else {
            this.setState({
                result: "Error",
                textColor:"red"
            })
        }
    }

    render() {
        const { auditCheck, cashIn, bonus, winLose, outstanding, cashOut, result } = this.state

        return (
            <div className="">
                <div className="col-xs-12">
                    <table className="table-bordered">
                        <tr>
                            <td>
                                Cash In Balance After:
                        </td>
                            <td>
                                <input type="number" value={cashIn} onChange={this.handleCashInChange} />
                            </td>
                            <td rowSpan="7">
                                <h3 className="p-2" style={{color:this.state.textColor}}>{result}</h3>
                            </td>
                        </tr>
                        <tr>
                            <td>
                                Bonus:
                        </td>
                            <td>
                                <input type="number" value={bonus} onChange={this.handleBonusChange} />
                            </td>
                        </tr>
                        <tr>
                            <td>
                                Win/Loss Amount:
                        </td>
                            <td>
                                <input type="number" value={winLose} onChange={this.handleWinLoseChange} />
                            </td>
                        </tr>
                        <tr>
                            <td>
                                Outstanding Amount:
                        </td>
                            <td>
                                <input type="number" value={outstanding} onChange={this.handleOutstandingChange} />
                            </td>
                        </tr>
                        <tr>
                            <td>
                                Cash Out Balance Before:
                        </td>
                            <td>
                                <input type="number" value={cashOut} onChange={this.handleCashOutChange} />
                            </td>
                        </tr>
                        <tr>
                            <td>
                                Audit Check:
                            </td>
                            <td className="text-right">
                                {auditCheck}
                            </td>
                        </tr>
                        <tr>
                            <td colspan="2"><button className="btn btn-primary btn-block" onClick={this.handleSubmit}>Submit</button></td>
                        </tr>
                    </table>

                </div>

            </div>

        )
    }
}

export default SimpleAuditCheck
